<template>
    <v-container id="template" >
        <v-btn id="print" class="blue white--text" @click="printReport()">Print</v-btn>
        <v-btn id="print" class="ml-4 red white--text" to="/bukutahunan" link>Back</v-btn>
        <v-btn v-if="statusButton" id="print" class="ml-4 green accent-4 white--text" @click="updateSaldo()">Update Saldo</v-btn>
       
       <template id="template">
           <br>
           <h2 class="text-center">Report {{saldo.JenisPerkiraan}}</h2><br>
           <v-row>
               <v-col cols="6">
                   <h4 class="text-left">No Perkiraan: {{saldo.KodeUnik}}</h4>
               </v-col>
               <v-col cols="6">
                   <h4 class="text-right">Nama Perkiraan: {{saldo.JenisPerkiraan}}</h4>
               </v-col>
           </v-row>

            <table id="table" width="750">
                <thead>
                    <tr style="font-size: 9pt">
                        <th id="th">Tanggal</th>
                        <th id="th">No Bukti</th>
                        <th id="th">Keterangan</th>
                        <th id="th">Kode Perkiraan</th>
                        <th id="th">Debet</th>
                        <th id="th">Kredit</th>
                        <th id="th">Saldo</th>
                        <!-- <th id="th">Total</th> -->
                    </tr>
                </thead>
                <tbody>
                    <tr style="font-size: 9pt">
                        <td class="text-center" id="td">{{saldo["Tanggal"]}}</td>
                        <td id="td">Saldo</td>
                        <td id="td"></td>
                        <td id="td"></td>
                        <td id="td"></td>
                        <td id="td"></td>
                        <td class="text-right" id="td">{{saldo["Debet"] | currency}}</td>
                    </tr>
                    <tr v-for="item in reports" :key='item.id' style="font-size: 9pt">
                        <td class="text-center" width="75.5px" id="td" > {{item.tanggal}} </td>
                        <td classs="text-left" id="td" width="56.6px" v-if="item.nobukti === 'null'">kosong</td>
                        <td classs="text-left" id="td" width="56.6px" v-else>{{item.nobukti}}</td>
                        <td class="text-left"  id="td" width="190px">{{item.keterangan}}</td>
                        <td class="text-center" id="td" width="75px" v-if="item.kodekredit === $route.params.kode">{{ item["kodedebet"]}}</td>
                        <td class="text-center" id="td" width="75px" v-else-if="item.kodedebet === $route.params.kode">{{ item["kodekredit"]}}</td>
<!--                         
                        <td class="text-center" id="td" v-if="item.kodekredit === $route.params.kode" width="50px">{{item["kodedebet"].slice(3,5)}}</td>
                        <td class="text-center" id="td" v-else-if="item.kodedebet === $route.params.kode" width="50px">{{item["kodekredit"].slice(3,5)}}</td>
                        
                        <td class="text-center" id="td" v-if="item.kodekredit === $route.params.kode" width="50px">{{item["kodedebet"].slice(5,9) }}</td>
                        <td class="text-center" id="td" v-else-if="item.kodedebet === $route.params.kode" width="50px">{{item["kodekredit"].slice(5,9) }}</td>
                         -->
                        <td class="text-right" id="td" width="150px">Rp {{item.kredit | currency}}</td>
                        <!-- <td v-else id="td"></td> -->
                        <td class="text-right" id="td" width="150px">Rp {{item.debet | currency}}</td>
                        <!-- <td v-else id="td"></td> -->
                        <td class="text-right" id="td" width="150px">Rp {{item.total | currency}}</td>
                    </tr>
                    <tr style="font-size: 9pt">
                        <td class="text-right" id="td" colspan="4">Total</td>
                        <td class="text-right" id="td">Rp {{totalKredit | currency}}</td>
                        <td class="text-right" id="td">Rp {{totalDebet | currency}}</td>
                        <td class="text-right" id="td">Rp {{totalSeluruh | currency}}</td>
                    </tr>
                </tbody>
            </table>
       </template>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                saldo:{},
                keyword: '',
                load: false,
                headers: [{
                    text: 'No',
                    value: 'no',
                },
                {
                    text: 'Tanggal',
                    value: 'tanggal'
                },
                {
                    text: 'No Bukti',
                    value: 'nobukti'
                },
                {
                    text: 'Keterangan',
                    value: 'keterangan'
                },
                {
                    text: 'Kode Debit',
                    value: 'kodedebet'
                },
                {
                    text: 'Kode Kredit',
                    value: 'kodekredit'
                },
                {
                    text: 'Debet',
                    value: 'debet'
                },
                {
                    text: 'Kredit',
                    value: 'kredit'
                },
                ],
                reports: [],
                totalpembukuans: '',
                saldoTable:0,
                total :0,
                totalDebet :0,
                totalKredit :0,
                totalSeluruh :0,
                statusButton : false,
                updatedId: '',
                form: {
                    jumlah: '',
                    debet: '',
                    kredit: '',
                    saldo: '',
                },
                listperkiraan: new FormData,
            }
        },
        methods: {
            getData() {
                var uri = this.$apiUrl + 'reportjurnaltahunan/' + this.$route.params.kode
                this.$http.get(uri).then(response => {
                    this.reports = response.data
                    var ur2 = this.$apiUrl + 'listperkiraan/' + this.$route.params.kode
                    this.$http.get(ur2).then(response => {
                        this.saldo = response.data[0]
                        this.saldoTable = this.saldo.Debet
                        for (var x in this.reports) {
                            if (this.reports[x].kodekredit === this.$route.params.kode) {
                                // // ini dibalik semua, normalnya this.totalSeluruh -= this.reports[x].jumlah untuk kredit, untuk debet di plus
                                this.saldoTable -= this.reports[x].debet
                                this.saldoTable += this.reports[x].kredit
                                this.reports[x].total = this.saldoTable
                                this.totalDebet += this.reports[x].debet
                                this.totalKredit += this.reports[x].kredit
                                this.totalSeluruh += this.reports[x].kredit
                                this.totalSeluruh -= this.reports[x].debet

                            } else if (this.reports[x].kodedebet === this.$route.params.kode) {
                                this.saldoTable -= this.reports[x].debet
                                this.saldoTable += this.reports[x].kredit
                                this.reports[x].total = this.saldoTable
                                this.totalDebet += this.reports[x].debet
                                this.totalKredit += this.reports[x].kredit
                                this.totalSeluruh += this.reports[x].kredit
                                this.totalSeluruh -= this.reports[x].debet
                            }                            
                        }
                        this.totalSeluruh += this.saldo.Debet
                    })
                })
            },

            
            printReport() {
                window.print()
                this.statusButton = true;
            },

            updateSaldo() {
               
                // this.form.debet = this.totalDebet;
                // this.form.kredit = this.totalKredit;
                // this.form.saldo = this.totalSeluruh;
                this.updatedId = this.$route.params.kode
                // console.log(this.updatedId);
                this.listperkiraan.append('jenisperkiraan', this.saldo.JenisPerkiraan);
                this.listperkiraan.append('kodeunik', this.saldo.KodeUnik);
                this.listperkiraan.append('kumulatifdebet', this.totalDebet);
                this.listperkiraan.append('kumulatifkredit', this.totalKredit);
                if(this.totalSeluruh < 0){
                    this.listperkiraan.append('kredit', this.totalSeluruh);
                }else{
                    this.listperkiraan.append('debet',this.totalSeluruh);
                }
                var uri = this.$apiUrl + 'listperkiraan/' + this.updatedId;
                this.load = true
                this.$http.post(uri, this.listperkiraan).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = response.data; //memasukkan pesan ke snackba
                    this.load = false;
                    this.dialog = false
                    this.statusUpdate = true
                    this.statusButton = false
                    this.typeInput = 'new';
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                    this.load = false;
                    this.typeInput = 'new';
                })

            },
        },
        mounted() {
            this.getData();
        },
    }
</script>
<style>
    @media print{
        #print{
            display: none;
        }
        #template{
            background-color: white
        }
    }
    
    #table, #th, #td {
        border: 1.5px solid black;
        border-collapse: collapse;
    }
</style>